header {

}
.logo {
    margin: 50px 20px;
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background: #1f73ef;
    box-shadow: inset 8px 8px 10px #1c68d7,
            inset -8px -8px 10px #227fff;
    a {
        width: 80px;
        height: 80px; 
    }
}
.download {
    margin: 45px 20px;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background: #1f73ef;
    box-shadow: inset 8px 8px 10px #1c68d7,
            inset -8px -8px 10px #227fff;
    svg {
        width: 50px;
        height: 50px; 
    }
}

.logo, .download {
    cursor: pointer;
}