main {
    background: #bbd5fa;
    background: #f3f3f3;
    width: 100%;
    box-sizing: border-box;
    padding: 50px;

    .main > div:last-child {
        display: flex;
        justify-content: center;
        margin: auto;

        // background: white;
    }

    .main {
        display: flex;
        background: white;
        height: 100%;

        border-radius: 10px;
        background: #f3f3f3;
        box-shadow: inset 10px 10px 20px #cccccc,
            inset -10px -10px 20px #ffffff;

        .colors {
            display: flex;
            flex-direction: column;
            margin-left: 50px;

            .color-picker-button {
                cursor: pointer;
                display: initial;
                height: 100%;
                margin-bottom : 50px;
                border-radius: 50px;
            }
            .color-picker-button:first-child {
                margin-top: 50px;
                // border-radius: 10px 0 0 0;
            }
            // .color-picker-button:last-child {
            //     border-radius: 0 0 0 10px;
            // }
        }
        .react-colorful {
            margin: 50px;
            margin-left: 130px;
            position: absolute;
        }
    }
}

@media (max-width: 1024px) {
    .app {
        flex-direction: column;
        height: 100vh;
        header {
            flex-direction: row;
            height: auto;
            

            .middle {
                display: flex;
            }
            .logo, .download {
                width: 70px;
                height: 70px;
                margin: 15px 20px;
                a {
                    height: 50px;
                    width: 50px;
                }
            }
        }
        main {
            padding: 0px;
            height: 100%;
            .main {
                height: 100%;
                border-radius: 0;
                .colors {
                    margin-left: 20px;
                }
                .react-colorful {
                    height: 11vh;
                    width: 100%;
                    margin: 0;
                    .react-colorful__saturation, .react-colorful__last-control {
                        border-bottom: 0px;
                        border-radius: 0;
                    }
                }
            }
        }
        footer {
            height: auto;

            section {
                flex-direction: column;
                align-items: flex-start;
                overflow: hidden;
                .style, .type {
                    margin-top: 0px;
                    display: flex;
                    position: relative;
                    .typeElement, .styleElement {
                        margin: 10px;
                        width: 60px;
                        height: 60px;
                    }
                    .styleElement {
                        margin-top: 15px;
                        margin-bottom: 0px;
                    }
                    .typeElement {
                        margin-top: 15px;
                        margin-bottom: 15px;
                    }
                    .typeElement:first-child, .styleElement:first-child {
                        margin-left: 20px;
                    }
                    .typeElement:last-child, .styleElement:last-child {
                        margin-right: 20px;
                    }
                }
            }
        }
    }
}