section {
    display: flex;
}
footer, header {
    height: 100vh;
    background: #1f73ef;
}
body {
    background: #bbd5fa;
}
header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.male {
    color: rgb(0, 238, 255);
    font-size: 30px;
}
.female {
    color: pink;
    font-size: 30px;
}
.typeElement {
    color: white;
}
.pose, .typeElement, .styleElement {
    cursor: pointer;

    border-radius: 10px;
    background: linear-gradient(145deg, #1c68d7, #217bff);
    box-shadow:  5px 5px 15px #1c68d7,
                    -5px -5px 15px #227fff;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    margin: 30px;
}
.styleElement {
    margin-right: 0;
}
.style {
    transition: 1s;
}